body {
  background-color: #fff; }

.todo-list {
  width: 320px;
  padding: 0 10px;
  font-family: 'Microsoft JhengHei';
  border-radius: 5px;
  margin: 10px auto; }

.todo-list > h1 {
  text-align: center;
  margin: 8px 0 12px;
  padding-bottom: 4px;
  font-size: 28px;
  font-weight: normal;
  color: #68d;
  border-bottom: 1px solid #ddd; }

.todo-form {
  text-align: center; }

.todo-form input {
  width: 280px;
  padding: 8px 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  font-family: 'Microsoft JhengHei';
  color: #336; }

@-webkit-keyframes show {
  from {
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px); }
  to {
    opacity: 1;
    -webkit-transform: none;
            transform: none; } }

@keyframes show {
  from {
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px); }
  to {
    opacity: 1;
    -webkit-transform: none;
            transform: none; } }

.todo-items {
  padding-left: 0;
  list-style: none; }

.todo-item {
  border-bottom: 1px solid #ddd;
  padding: 24px 12px;
  font-size: 16px;
  cursor: pointer;
  color: #333;
  list-style: none;
  -webkit-transition: background-color .3s;
  -o-transition: background-color .3s;
  transition: background-color .3s;
  /* animation: show .3s ease-in-out; */ }

.todo-item:first-child {
  border-top: 1px solid #ddd; }

.todo-item:before {
  vertical-align: middle;
  margin-right: 24px;
  content: url("../../assets/images/check0.png"); }

.todo-item.done:before {
  content: url("../../assets/images/check1.png"); }

.todo-item.done {
  text-decoration: line-through;
  color: #888;
  background-color: #f6f6f6; }



